.side-drawer {
	background:white;
	box-shadow: 1px  0px 3px rgb(0,0,0);
	position:fixed;
	top:80px;
	left:0;
	width: 70%;
	max-width: 400px;
	z-index:200;
	transform: translateX(-100%);
	transition: transform 0.3s ease-out;
}

.side-drawer ul {
	height: 100%;
	list-style: none;
	display: flex;
	flex-direction: column;
	justify-content: center;
	padding-left: 0;
}

.side-drawer li {
	margin: 0.5rem 0.5rem;
	text-align: center;
}

.side-drawer a {
	color: #B67920;
	text-decoration: none;
	font-size: 1.2rem;
}

.side-drawer a:hover {
	color:#B4ADA5;
}

.side-drawer.open {
	transform: translateX(0);
}

@media(min-width:769px) {
	.side-drawer{
		display: none;
	}
}

hr {
	border: 0;
	clear:both;
	display:block;
	width: 100%;
	background-color:grey;
	height: 1.3px;
}
