	text-align: left;
	border:grey solid 2px;
	overflow:auto;
}

.listofRecipes .recipe_Box:hover {
	background: #f8f8f8;
	cursor: pointer;
}

.recipe_title {
	font-family: 'Rajdhani';
	color:#B67920;;
	text-align: center;
	letter-spacing: 3px;
}

.recipe_ingredients {
	font-family: 'Rajdhani';
	color:#B67920;;
	letter-spacing: 2px;
}

.recipes_list input {
	display: block;
	margin:0 auto;
	border: 0;
	border-bottom:3px solid #9ba9b2;
	outline: 0;
	background:transparent;
	width: 50%;
	font-weight: lighter;
	letter-spacing: 2px;
	font-size: 15px;
	text-align: center;
	margin-bottom:50px;
	padding-top:0;
}

 .recipes_list input:focus {
	border-bottom: 4px solid #A1C4C8;

}

.fa-search {
	font-size: 2rem;
	color: #A1C4C8;
	padding-left:25%;
}

.recipeContainer{
	background-image:url(/static/media/recipeBG.312c9af7.png);
	background-size: cover;
	background-repeat: no-repeat;
