/*form submit button */
.recipeContainer input[type="submit"] {
	border: 0;
	padding: 10px 30px;
	margin:10px;
	margin-left:45%;
	border-radius: 3px;
	font-weight: lighter;
	letter-spacing: 1px;
	font-size: 18px;
	cursor: pointer;
	background-color: #F0B31E;
	color: #fff;
	font-weight: 100;
}

input[type="button"][value="Cookies"] {
	background: url(/static/media/cookiesBtn.8a3e854b.jpg) no-repeat scroll 0 0 transparent;
	background-size: 100%; /* To fill the dimensions of container (button), or */
}

input[type="button"][value="Bread"] {
	background: url(/static/media/breadBtn.44baeb00.jpg) no-repeat scroll 0 0 transparent;
	background-size: 100%; /* To fill the dimensions of container (button), or */
}

input[type="button"][value="Cake"] {
	background: url(/static/media/cakeBtn.7416003b.jpg) no-repeat scroll 0 0 transparent;
	background-size: 100%; /* To fill the dimensions of container (button), or */
}

input[type="button"][value="Cookies"],input[type="button"][value="Bread"],input[type="button"][value="Cake"] {
	cursor: pointer;
	padding: 65px 130px;
	margin-right: 30px;
	border: 3px solid #A1C4C8;
	color: #fff;
	text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
	font-family: 'Dekko', cursive;
	font-size: 30px;
	letter-spacing: 4px;
	font-weight: 900;
	width:30%;
	border-radius: 10px;
	text-align: center;
}

input[type="button"][value="Cookies"]:hover {
	background-image:linear-gradient(rgba(255,255,255,0.5), rgba(255,255,255,0.5)), url(/static/media/cookiesBtn.8a3e854b.jpg)
}

input[type="button"][value="Bread"]:hover {
	background-image:linear-gradient(rgba(255,255,255,0.5), rgba(255,255,255,0.5)), url(/static/media/breadBtn.44baeb00.jpg)
}

input[type="button"][value="Cake"]:hover {
	background-image:linear-gradient(rgba(255,255,255,0.5), rgba(255,255,255,0.5)), url(/static/media/cakeBtn.7416003b.jpg)
}

